'use client';
import Link from 'next/link';
import { useSession, signOut } from 'next-auth/react';

export default function Navbar() {
    const { data: session } = useSession();

    return (
        <nav className="bg-gray-800 text-white px-6 py-4">
            <div className="max-w-7xl mx-auto flex justify-between items-center">
                <div className="font-bold text-xl">Uotel</div>

                <div className="space-x-4">
                    <Link href="/" className="hover:text-gray-400">首页</Link>

                    {session ? (
                        <>
                            <span>欢迎, {session.user?.name}</span>
                            <button onClick={() => signOut()} className="hover:text-gray-400">退出登录</button>
                        </>
                    ) : (
                        <>
                            <Link href="/login" className="hover:text-gray-400">登录</Link>
                            <Link href="/register" className="hover:text-gray-400">注册</Link>
                        </>
                    )}
                </div>
            </div>
        </nav>
    );
}